import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/views/layout/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: layout,
      children:[
        {
          path:'/news',
          name:'新闻列表页',
          component:()=>import('@/views/news/news_list')
        },
        {
          path:'/news/add',
          name:'新闻添加页',
          component:()=>import('@/views/news/news_add')
        },
        {
          path:'/news/update',
          name:'新闻修改页',
          component:()=>import('@/views/news/news_add')
        },
        {
          path:'/case',
          name:'工程列表页',
          component:()=>import('@/views/case/case_list')
        },
        {
          path:'/case/add',
          name:'工程添加页',
          component:()=>import('@/views/case/case_add')
        },
        {
          path:'/case/update',
          name:'工程修改页',
          component:()=>import('@/views/case/case_add')
        }
        ,
        {
          path:'/swiper',
          name:'轮播图列表页',
          component:()=>import('@/views/swiper/swiper_list')
        },
        {
          path:'/swiper/add',
          name:'轮播图添加页',
          component:()=>import('@/views/swiper/swiper_add')
        },
        {
          path:'/swiper/update',
          name:'轮播图修改页',
          component:()=>import('@/views/swiper/swiper_add')
        },
        {
          path:'/clients',
          name:'客户列表页',
          component:()=>import('@/views/clients/clients_list')
        },
        {
          path:'/clients/add',
          name:'客户添加页',
          component:()=>import('@/views/clients/clients_add')
        },
        {
          path:'/clients/update',
          name:'客户修改页',
          component:()=>import('@/views/clients/clients_add')
        }
        
      ]
    },
    {
      path:'/admin',
      name:'管理员登录',
      component:()=>import('@/views/adminLogin')
    }
  ]
})



