<template>
    <div class="layout">
      <headerBar></headerBar>
      <sideBar></sideBar>
      <router-view></router-view>
      <h2>欢迎来到后台管理！</h2>  
      <div class="container">
        <a>@xingshize</a>                  
      </div>
    </div>
</template>

<script>
    import sideBar from './components/side-bar';
    import headerBar from './components/header-bar';
    export default {
        name: "index",
        components:{
          sideBar,
          headerBar
        },
        methods:{
          isLogin() {
            this.$axios.get('adminIsLogin').then(res => {
              if(res.code != 200) {
                this.$router.push('/admin')
              }
            })
          }
        },
        mounted() {
          this.isLogin()
        }
    }
</script>

<style scoped>
.layout{
  padding: 55px 0 0 230px;
}
h2{
  margin: 20px auto 0;
  padding-top:20px;
  text-align: center;
  color: grey;
  border-top: 1px solid #f1f1f1;
}
.container{
  text-align: center;
}
a{
  display:inline-block;
  margin: 10px auto 30px;
  color:grey;
  cursor:pointer;
}
a:hover{
  color:#2e6da4;
}
</style>



