<template>
    <div class="header-bar">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item v-for="(item,index) in routeList" :to="item.path" :key="index">{{item.name}}</el-breadcrumb-item>
      </el-breadcrumb>
      <el-button size="mini" class="btn" @click="logout">退出登录</el-button> 
    </div>
</template>

<script>
    export default {
        name: "header-bar",
        data() {
          return {
            routeList:[]
          }
        },
        methods:{
          getList(){
            //$route.matched保存所有路由的信息，filter是筛选信息（有name的路由传值，没有的跳过）
            this.routeList = this.$route.matched.filter(item =>item.name)
          },
          logout() {
            this.$axios.get('adminLogout').then(res => {
              if(res.code == 200) {
                this.$router.push('/admin')
              }
            })
          }
        },
        watch:{//监听$route,如果发生变化，则执行getList函数
          $route(){
            this.getList()
          }
        },
        created(){ //在模板渲染成html前调用，即通常初始化某些属性值，然后再渲染成视图。
          this.getList()
        }
    }
</script>
<style>
.btn{
  position: fixed;
  right:30px;
  top:15px;
}
</style>

<style scoped>
.header-bar{
  position:fixed;
  left:230px;
  right: 0;
  top:0;
  padding: 20px;
  border-bottom: 1px solid #f1f1f1;
  background-color:#fff;
  z-index:1000;
}
</style>



